import React, { Component } from 'react';
import { connect } from 'react-redux';

import { getFeaturedPieces } from '../actions';

import Nav from './Nav';
import Footer from './Footer';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    
    getFeaturedPieces() {
        console.log(this.props.getFeaturedPieces())
    }

    render() {
        return (
            <div>
                <Nav />
                {this.props.children}
                <Footer />
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        reducers: state
    }
}

export default connect(mapStateToProps, {getFeaturedPieces})(App);


