import React, { Component } from 'react';
import { Link } from 'react-router';

class DiscoverPiece extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        // console.log(this.props.piece)
        let { piece } = this.props;
        let pieceImg = piece.imgUrls[Object.keys(piece.imgUrls)[0]]
        return (
            <div className="row discoverPiece">
                <div className="daPiece col-md-6 col-md-offset-3 col-sm-10 col-sm-offset-1">
                    <Link to={'/artist/' + piece.artistUsername + '/' + piece.pieceId}>
                        <img src={pieceImg} alt=""/>

                        {/* <p className="price">${piece.price}</p> */}
                        <div className="row">
                            <div className="col-sm-8 colLeft">
                                {/* <p className="small">Piece Name</p> */}
                                <p className="big">{piece.pieceName} <span className="mid">by {piece.artistUsername}</span></p>
                            </div>
                            <div className="col-sm-4 colRight">
                                {/* <p className="small">Artist Username</p> */}
                                <p className="price">${piece.price}</p>
                            </div>
                        </div>
                        <hr/>
                    </Link>
                </div>
            </div>
        )
    }
}

export default DiscoverPiece;


