import React from 'react';
import ReactDOM from 'react-dom';
import Amplitude from 'react-amplitude';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import { Router, hashHistory, Route, Redirect } from 'react-router';
import { createStore, applyMiddleware } from 'redux';

import App from './components/App';
import Home from './components/Home';
import Story from './components/Story';
import Artists from './components/artists/Artists';
import ArtistProfile from './components/artists/ArtistProfile';
import ArtPiece from './components/artists/ArtPiece';
import Discover from './components/Discover';

import './index.css';
import reducers from './reducers';
const store = createStore(reducers, applyMiddleware(thunk))

Amplitude.init('5e09ac32b11544d51dece40b53c6b27d');
export const _amp = Amplitude;

document.addEventListener('DOMContentLoaded', function() {
    ReactDOM.render(
        <Provider store={store}>
            <Router history={hashHistory}>
                <Redirect from="/" to="story" />
                <Route path="/" component={App}>
                    <Route path="home" component={Home} /> 
                    <Route path="story" component={Story} /> 
                    <Route path="artists" component={Artists} /> 
                    <Route path="artist/:id" component={ArtistProfile} /> 
                    <Route path="artist/:id/:pieceId" component={ArtPiece} /> 
                    <Route path="discover" component={Discover} /> 
                </Route>
            </Router>
        </Provider>,
        document.getElementById('root')
    )
});


