import React, { Component } from 'react';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        return (
            <div className="jumbotron jumbotron-fluid">
                <div className="container">
                    <h1 className="display-3">Welcome to Muralist</h1>
                    <p className="lead">Toronto's Art share platform | Coming soon</p>
                </div>
            </div>
        )
    }
}

export default Home;


