import { GET_FEATURED_PIECES, GET_ARTISTS, SUBSCRIBE_USER, GET_ARTIST_INFO, GET_PIECE_INFO, POST_PURCHASE_FORM, PULL_FEED } from '../constants';
import axios from 'axios';

export const getFeaturedPieces = () => {
    const action = {
        type: GET_FEATURED_PIECES,
        pieces: 'Yay'
    }
    return action;
}

export const subscribeEmail = (userEmail) => {
    return (dispatch) => {
        return axios.get('https://us-central1-art-auction-2ef27.cloudfunctions.net/newEmailSub?email=' + userEmail).then((response) => {
            // console.log(response.status);
            dispatch({
                type: SUBSCRIBE_USER,
                response: response.data
            })
        })
    }
}

export const getArtists = () => {

    return (dispatch) => {
        return axios.get('https://us-central1-art-auction-2ef27.cloudfunctions.net/getAllArtists').then((response) => {
            dispatch({
                type: GET_ARTISTS,
                artists: response.data
            });
        })
    }
}

export const getArtistInfo = (artistUsername) => {
    return (dispatch) => {
        return axios.get('https://us-central1-art-auction-2ef27.cloudfunctions.net/getArtistInfo?artistUsername=' + artistUsername).then((response) => {
            dispatch({
                type: GET_ARTIST_INFO,
                artistInfo: response.data
            });
        })
    }
}

export const getArtPiece = (artistUsername, pieceId, src) => {
    return (dispatch) => {
        return axios.get('https://us-central1-art-auction-2ef27.cloudfunctions.net/getPieceInfo3?artistUsername=' + artistUsername + '&pieceId=' + pieceId).then((response) => {
            dispatch({
                type: GET_PIECE_INFO,
                pieceInfo: response.data
            })
        })
    }
}

export const submitPurchaseBidForm = (submissionObj) => {
    return (dispatch) => {
        return axios.post('https://us-central1-art-auction-2ef27.cloudfunctions.net/submitPurchaseForm', submissionObj).then(response => {
            dispatch({
                type: POST_PURCHASE_FORM,
                postResponse: response.data
            })
        })
    }
}

export const pullFeed = () => {
    return (dispatch) => {
        return axios.get('https://us-central1-art-auction-2ef27.cloudfunctions.net/feedPull').then(response => {
            dispatch({
                type: PULL_FEED,
                pulledFeed: response.data
            })
        })
    }
}



