import { GET_FEATURED_PIECES, GET_ARTISTS, SUBSCRIBE_USER, GET_ARTIST_INFO, GET_PIECE_INFO, POST_PURCHASE_FORM, PULL_FEED } from '../constants';

const reducers = (state = {}, action) => {
    let featuredPieces = null;
    state = {}

    switch(action.type) {
        case GET_FEATURED_PIECES:
            featuredPieces = [...state, 'piece name'];
            return featuredPieces;

        case GET_ARTISTS:
            return {...state, artists: action.artists};

        case GET_ARTIST_INFO:
            return {...state, artistInfo: action.artistInfo};

        case GET_PIECE_INFO:
            return {...state, pieceInfo: action.pieceInfo};

        case SUBSCRIBE_USER:
            return {...state, subscribeUserSuccess: true}

        case POST_PURCHASE_FORM:
            return {...state, postResponse: action.postResponse}

        case PULL_FEED:
            return {...state, pulledFeed: action.pulledFeed}

        default:
            return state;
    }
}

export default reducers;

